.main{
    margin-top: 2rem;
    margin-bottom: 2rem;
}
.column {
    float: left;
    width: 33%;
    padding: 0 10px;
  }
  .row {margin: 0 -5px;}
  .row:after {
    content: "";
    display: table;
    clear: both;
  }
  .card {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    padding: 16px;
    text-align: center;
    background-color: #f1f1f1;
  }
  @media screen and (max-width: 860px) {
    .column {
      width: 100%;
      display: block;
      margin-bottom: 20px;
    }
  }
 
  .img{
    width:100%;
    height: 300px;
    border-radius: 1rem;
  }
  .button{
    background-color: rgb(26, 203, 82);
    color:white;
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: 0.3rem;
    padding-bottom: 0.3rem;
    border:none;
    margin-left: 20%;
    margin-right:5%;
    font-size: 1.3rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
    border-radius: 0.3rem;
    
  }
  .button:hover{
    background-color: rgb(156, 233, 12);
  }
  .price{
    font-size: 1.3rem;
    padding-left: 3%;
  }
  .title{
    font-size: 1.5rem;
    padding-left: 5%;
    padding-bottom: 0.3 rem;
    padding-top: 0.2 rem;
    font-style: Bold;
  }
  .text{
    font-size: 1rem;
    padding-left: 5%;
    padding-top: 0.1rem;
    font-style:normal ;
  }
