.header {
    
    top: 0;
    left: 0;
    width: 100%;
    height: 5rem;
    background-color: #068a1a;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10%;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    z-index: 10;
  }
  
  .main-image {
    width: 100%;
    height: 25rem;
    z-index: 0;
    overflow: hidden;
  }
  
  .main-image img {
    width: 110%;
    height: 100%;
    object-fit: cover;
    transform: rotateZ(-5deg) translateY(-4rem) translateX(-1rem);
  }
  .btn{
    padding: 1rem;
    border-radius: 1rem;
    border: none;
    padding-left: 2rem;
    padding-right: 2rem;
    background-color: #40B554;
    font-size: 1.5rem;
    color:white;
}
